



































import Vue from "vue";
export default Vue.extend({
    name: "QVideo",
    props: {
        src: {
            type: String,
        },
        controls: {
            type: Boolean,
            default: true
        },
        width: {
            type: String
        },
        height: {
            type: String
        },
        autoPlay: {
            type: Boolean,
            default: false
        },
        muted: {
            type: Boolean,
            default: false
        },
        loop: {
            type: Boolean,
            default: false
        },
        controlsList: {
            type: String
        },
        poster: {
            type: String
        },
        preload: {
            type: String
        },
        
    },
    methods: {
        play(){
            (this.$refs['videoRef'] as HTMLVideoElement).play();
        },
        pause(){
           (this.$refs['videoRef'] as HTMLVideoElement).pause();
        },
        load(){
            (this.$refs['videoRef'] as HTMLVideoElement).load();
        },
        currentTime(second?: number){
            if(second) {
                (this.$refs['videoRef'] as HTMLVideoElement).currentTime = second;
            } else {
                return (this.$refs['videoRef'] as HTMLVideoElement).currentTime;
            }
        },
        duration(){
            return (this.$refs['videoRef'] as HTMLVideoElement).duration;
        },
        currentSrc(){
            return (this.$refs['videoRef'] as HTMLVideoElement).currentSrc;
        },
        volume(volume?: number){
            if(volume) {
                (this.$refs['videoRef'] as HTMLVideoElement).volume = volume;
            } else {
                return (this.$refs['videoRef'] as HTMLVideoElement).volume;
            }
        },
        ended(){
            this.$emit("ended");
        },
        durationChange(){
            this.$emit("durationChange");
        },
        onPlay(){
            this.$emit("play");
        },
        onPause(){
            this.$emit("pause");
        },
        onPlaying(){
            this.$emit("playing");
        },
        progress(){
            this.$emit("progress");
        },
        volumeChange(){
            this.$emit("volumeChange");
        },
        rateChange(){
            this.$emit("rateChange");
        },
        emptied(){
            this.$emit("emptied");
        },
        stalled(){
            this.$emit("stalled");
        },
        suspend(){
            this.$emit("suspend");
        },
        timeUpdate(){
            this.$emit("timeUpdate");
        },
        canPlay(){
            this.$emit("canPlay");
        },
        canPlayThrough(){
            this.$emit("canPlayThrough");
        },
        waiting(){
            this.$emit("waiting");
        },
        error(){
            this.$emit("error");
        }
    },
    destroyed() {
      this.$el?.remove();
   }
    
});
